.preloader {
	padding: 0;
	position: relative;
	margin: auto;
	z-index: 100;
}
.circle-preloader {
	display: block;
	width: 50px;
	height: 50px;
	border: 5px solid #444;
	border-bottom-color: #888;
	border-radius: 50%;
	margin: auto;
	animation: spin .75s infinite linear;
}
@keyframes spin {
	100% {
		transform: rotate(360deg);
	}
}